import EventSystem from "../../../kit/system/event/EventSystem";
import ExploreBaseGame from "./ExploreBaseGame";
import ExploreGameAudio from "./ExploreGameAudio";
import ExploreGameData, { ExploreGameType } from "./ExploreGameData";
import ExploreGameUtil from "./ExploreGameUtil";
import ExploreTigo from "./ExploreTigo";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ExploreControl extends cc.Component {
   /** 游戏列表跟节点 */
   private gameNode: cc.Node = null;

   private tigoNode: cc.Node = null;

   private scaleCamera: boolean;
   private narrowCamera: boolean;

   private camera: cc.Camera;

   private subGameMap: Map<ExploreGameType, ExploreBaseGame>;

   private subGameZoomRatio: number

   private exitPos: cc.Vec3;

   private playedIndex = [];

   private exploreTigo: ExploreTigo;
   
   onLoad () {
      cc.director.getPhysicsManager().enabled = true 
      cc.director.getCollisionManager().enabledDebugDraw = true;
   }

   start () {
      ExploreGameAudio.instance.bgm();
      let mainCamera = cc.Canvas.instance.node.getChildByName("Main Camera").getComponent(cc.Camera);
      mainCamera.clearFlags = 6;
      mainCamera.cullingMask &= ~(1 << 1); // 关闭层x

      this.camera = this.node.getChildByName("camera").getComponent(cc.Camera);
      this.camera.zoomRatio = 1.7;
      this.camera.node.position = cc.v3(-374, -128);
      this.gameNode = this.node.getChildByName("gameNode");
      this.tigoNode = this.node.getChildByName("player_tigo");
      this.initSubGame();
      if (!this.tigoNode) {
         cc.error("tigoNode null");
         return;
      }
      this.exploreTigo = this.tigoNode.addComponent(ExploreTigo);
      this.exploreTigo.init();
      cc.log(this.exploreTigo);
      this.onTigoIn();

      EventSystem.on(ExploreGameData.HidePlayer, this.hideTigoNode, this);
      EventSystem.on(ExploreGameData.SubGameExit, this.onSubGameExit, this);
      EventSystem.on(ExploreGameData.PlayerMove, this.checkGameNode, this);
      EventSystem.on(ExploreGameData.MoveTiGo, this.moveTigo, this);
      EventSystem.on(ExploreGameData.PlayerMoveStart, this.PlayerMoveStart, this);
      EventSystem.on(ExploreGameData.PlayerMoveEnd, this.playerMoveEnd, this);
   }

   update (dt) {
      if (this.scaleCamera) {
         this.camera.zoomRatio -= dt;
         if (this.camera.zoomRatio <= 1) {
            this.camera.zoomRatio = 1;
            this.scaleCamera = false
         }
      }

      if (this.narrowCamera) {
         this.camera.zoomRatio += dt * 0.5;
         if (this.camera.zoomRatio >= this.subGameZoomRatio) {
            this.camera.zoomRatio = this.subGameZoomRatio;
            this.narrowCamera = false
         }
      }
   }

   onDestroy () {
      cc.director.getPhysicsManager().enabled = false;
      EventSystem.off(ExploreGameData.HidePlayer, this.hideTigoNode, this);
      EventSystem.off(ExploreGameData.SubGameExit, this.onSubGameExit, this);
      EventSystem.off(ExploreGameData.PlayerMove, this.checkGameNode, this);
      EventSystem.off(ExploreGameData.MoveTiGo, this.moveTigo, this);
      EventSystem.off(ExploreGameData.PlayerMoveStart, this.PlayerMoveStart, this);
      EventSystem.off(ExploreGameData.PlayerMoveEnd, this.playerMoveEnd, this);
      let mainCamera = cc.Canvas.instance.node.getChildByName("Main Camera").getComponent(cc.Camera);
      mainCamera.clearFlags = 7;
      mainCamera.cullingMask |= (1 << 1);  // 打开层x

      EventSystem.emit("hide_common_hand_guild");
   }

   private initSubGame () {
      this.subGameMap = new Map<ExploreGameType, ExploreBaseGame>();
      this.subGameMap.set(ExploreGameType.Xiaqi, this.node.getChildByName('xiaqi').getComponent(ExploreBaseGame));
      this.subGameMap.set(ExploreGameType.Yugang, this.node.getChildByName('yugang').getComponent(ExploreBaseGame));
      this.subGameMap.set(ExploreGameType.Budaoweng, this.node.getChildByName('budaoweng').getComponent(ExploreBaseGame));
      this.subGameMap.set(ExploreGameType.Renqiu, this.node.getChildByName('fangziyouxi').getComponent(ExploreBaseGame));
      this.subGameMap.set(ExploreGameType.PaoPaoji, this.node.getChildByName('paopaoji').getComponent(ExploreBaseGame));
   } 

   public onTigoIn () {
      ExploreGameData.instance.canTouch = false;
      this.exploreTigo.lutou();
      cc.tween(this.camera.node)
      .to(4, {x: 270})
      .start();

      cc.tween(this.tigoNode)
      .to(4, {x: 257})
      .call(() => {
         // 设置层级
         this.tigoNode.setSiblingIndex(this.node.childrenCount - 1);
         ExploreGameAudio.instance.playTigoInAudio();
         this.exploreTigo.appear(() => {
            this.scaleCamera = true;
            cc.tween(this.camera.node)
            .to(1, {y: this.tigoNode.y, x: this.tigoNode.x})
            .call(() => {
               ExploreGameData.instance.canTouch = true;
            })
            .start();
            ExploreGameAudio.instance.playMoveAudio();
            this.exploreTigo.idle();
         })
         
      }) 
      .start();
   }

   public changeGameIng (gameType: ExploreGameType) {
      let baseGame = this.subGameMap.get(gameType);
      this.exitPos = this.camera.node.position;
      if (baseGame) {
         ExploreGameAudio.instance.stopMoveAudio();
         baseGame.onEnter(this.camera);
         this.subGameZoomRatio = baseGame.cameraZoomRatio;
         if (baseGame.playerPos) {
            this.tigoNode.position = baseGame.playerPos;
         }
         this.narrowCamera = true;
         ExploreGameData.instance.canTouch = false;
      }
   }

   public onSubGameExit (eventData: any) {
      this.tigoNode.active = true;
      this.exploreTigo.setIdle();
      this.scaleCamera = true;
      ExploreGameAudio.instance.playMoveAudio();
      if (eventData.data && eventData.data.MoveCamera) {
         EventSystem.emit(ExploreGameData.MoveCamera, eventData.data);
         cc.tween(this.camera.node)
         .delay(0.7)
         .call(() => {
            ExploreGameData.instance.canTouch = true;
         })
         .delay(4)
         .call(() => {
            let index = this.playedIndex.splice(0, 1);
            for (let i = 0; i < this.gameNode.childrenCount; i ++) {
               if (index[0] == i) {
                  let child = this.gameNode.children[i];
                  child.active = true;
               }
            }
         })
         .start();
         return;
      }
      cc.tween(this.camera.node)
      .to(0.7, {x: this.exitPos.x, y: this.exitPos.y})
      .call(() => {
         EventSystem.emit(ExploreGameData.MoveCamera);
         ExploreGameData.instance.canTouch = true;
      })
      .delay(4)
      .call(() => {
         let index = this.playedIndex.splice(0, 1);
            for (let i = 0; i < this.gameNode.childrenCount; i ++) {
               if (index[0] == i) {
                  let child = this.gameNode.children[i];
                  child.active = true;
               }
            }
      })
      .start();
   }

   private hideTigoNode () {
      this.tigoNode.active = false;

   }

   private moveTigo (event: any) {
      cc.tween(this.tigoNode)
      .to(event.data.time, {x: event.data.x, y: event.data.y})
      .call(() => {
         this.exploreTigo.idle();
      })
      .start();
      this.tigoNode.scaleX = event.data.dir;
   }

   public playerMoveEnd () {
      this.exploreTigo.idle();
   }

   public PlayerMoveStart () {
      this.exploreTigo.move();
   }

   /** 检查是否到游戏点 */
   public checkGameNode (dir) {
      if (dir.data != 0) {
         this.exploreTigo.turn(dir.data);
         // this.tigoNode.scaleX = dir.data;
      }
      let tigoRect = new cc.Rect(this.tigoNode.x - this.tigoNode.width / 2, this.tigoNode.y - this.tigoNode.height / 2, this.tigoNode.width, this.tigoNode.height);
      for (let i = 0; i < this.gameNode.childrenCount; i ++) {
         let child = this.gameNode.children[i];
         if (!child.active) {
            continue;
         }
         let childRect = new cc.Rect(child.x, child.y, child.width, child.height);
         // if (ExploreGameUtil.intersection(tigoRect, childRect)) {
         if (tigoRect.contains(cc.v2(child.x, child.y))) {
            cc.log("碰撞");
            let gameType = ExploreGameUtil.gameTypeByName(child.name);
            this.changeGameIng(gameType);
            child.active = false;
            this.playedIndex.push(i);
         }
      }
   }
}

