import AudioManager from "../../../kit/system/audio/AudioManager";
import ExploreBaseGame from "./ExploreBaseGame";
import ExploreGameAudio from "./ExploreGameAudio";
import ExploreGameUtil from "./ExploreGameUtil";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameYugang extends ExploreBaseGame {

    public cameraZoomRatio = 1.5;

    public centerPos: cc.Vec3 = cc.v3(-1436, 0);
    public playerPos = cc.v3(-1609., -150);

    public onEnter(camera: cc.Camera) {
        super.onEnter(camera);
        this.scheduleOnce(() => {
            this.gaming();
        }, 0.5)
    }

    private gaming () {
        AudioManager.passMusic();
        let yugang = this.node.getChildByName('yugang');
        let spine = yugang.getComponent(sp.Skeleton);
        cc.tween(this.node)
        .delay(0.9)
        .call(() => {
            ExploreGameAudio.instance.playYuGang();
        })
        .start();
        ExploreGameUtil.playSpine(spine, 'yugang', false, () => {
            this.exit({MoveCamera: true, x: false, y: true});
            AudioManager.resumeMusic();
        });
    }
}
