
import ResourcesManager, { ResourceType } from "../../../kit/manager/ResourcesManager";
import EventSystem from "../../../kit/system/event/EventSystem";
import CameraMove from "./CameraMove";
import ExploreBaseGame from "./ExploreBaseGame";
import ExploreGameAudio from "./ExploreGameAudio";
import ExploreGameData from "./ExploreGameData";
import ExploreGameUtil from "./ExploreGameUtil";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GamePaoPaoJi extends ExploreBaseGame {

    public cameraZoomRatio = 1.5;

    public centerPos: cc.Vec3 = cc.v3(1313, -270);
    public playerPos: cc.Vec3 = cc.v3(1313, -270);


    private paopaoji: sp.Skeleton;
    private paopao: sp.Skeleton;

    private isTouch = false;

    public onEnter(camera: cc.Camera) {

        this.paopaoji = this.node.getChildByName("paopaoji").getComponent(sp.Skeleton);
        this.paopao = this.node.getChildByName("paopao").getComponent(sp.Skeleton);
        // this.centerPos = camera.node.position;
        // this.centerPos.y = -500
        //this.playerPos = camera.node.position;
        //super.onEnter(camera);
        if (!camera) {
            this.exit();
            return;
        }
        this.camera = camera;
        
        EventSystem.emit(ExploreGameData.HidePlayer);
        this.gaming();   
    }

    public exit (data?: any) {
        this.paopao.node.active = false;
        this.paopao.node.position = cc.v3(1616, -226);
        this.paopao.node.scale = 0.2;
        super.exit(data);
    }

    private loadGuide () {
        ResourcesManager.instance.loadRes("exploreGame", "prefabs/hand", ResourceType.Normal, cc.Prefab, (e, res) => {
            this.scheduleOnce(() => {
                if (this.isTouch) {
                    return;
                }
                let prefab = cc.instantiate(res) as cc.Node;
                this.node.addChild(prefab);
                prefab.position = this.paopao.node.position
                let spin = prefab.getComponentInChildren(sp.Skeleton);
                ExploreGameUtil.playSpine(spin, "dian", true);
                ExploreGameAudio.instance.playClick();
            }, 2)
        })
    }

    private gaming () {
        ExploreGameUtil.playSpine(this.paopaoji, 'paopaoji-chui', false, null, 1, 'paopaoji-chui-daiji1', true);
        cc.tween(this.camera.node)
        .to(0.6, {x: 1510, y: -651})
        .delay(1)
        .call(() => {
            ExploreGameAudio.instance.playPaopaoJi();
        })
        .to(2.6, {y: -260})
        .to(0.9, {x: 1313, y: -281})
        .delay(5)
        .call(() => {
            this.paopao.node.on(cc.Node.EventType.TOUCH_END, this.touchPaopao, this);
            this.loadGuide();
        })
        .start();

        this.scheduleOnce(() => {
            this.paopao.node.active = true;
            cc.tween(this.paopao.node)
            .to(0.4, {x: 1313, y: -281, scale: 0.8})
            .start();
            ExploreGameUtil.playSpine(this.paopao, 'daiji', true);
        }, 4.4)
    }

    private touchPaopao () {
        let hand = this.node.getChildByName("hand");
        if (hand) {
            hand.destroy();
        }
        ExploreGameAudio.instance.stopClick();
        this.unscheduleAllCallbacks();
        ExploreGameAudio.instance.playPaoPao();
        let anim = this.paopaoji.animation;
        this.paopaoji.setMix(anim, 'paopaoji-paopaozhakai', 0.2);
        ExploreGameUtil.playSpine(this.paopaoji, 'paopaoji-paopaozhakai', false);
        ExploreGameUtil.playSpine(this.paopao, 'zhakai', false, () => {
            this.exit({MoveCamera: true});
        });
        this.paopao.node.off(cc.Node.EventType.TOUCH_END, this.touchPaopao, this);
    }
}
