import AudioManager from "../../../kit/system/audio/AudioManager";

const {ccclass} = cc._decorator;

@ccclass
export default class ExploreGameAudio {
    private static _instance: ExploreGameAudio = null;
    public static get instance(): ExploreGameAudio {
        if (!ExploreGameAudio._instance) {
            ExploreGameAudio._instance = new ExploreGameAudio();
        }
        return ExploreGameAudio._instance;
    }

    private bundleName = 'exploreGame';

    public bgm () {
        AudioManager.playMusic(this.bundleName, "audio/BGM_shoushifangjian01")
    }

    // 按住
    public playPress () {
        AudioManager.playEffect(this.bundleName, "audio/press")
    }

    // 拖拽
    public playDrag () {
        AudioManager.playEffect(this.bundleName, "audio/drag")
    }

    // 点一点
    public playClick () {
        AudioManager.playEffect(this.bundleName, "audio/dianyidian_1")
    }

    public stopClick () {
        AudioManager.stopEffectByName(this.bundleName + "/audio/dianyidian_1")
    }

    /** tigo入场 */
    public playTigoInAudio () {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_01");
    }

    /** 竹蜻蜓 */
    public playMoveAudio() {
        AudioManager.playLoopEffect(this.bundleName, "audio/SE_tansuowanjuwu_02");
    }

    public stopMoveAudio() {
        AudioManager.stopLoopEffect(this.bundleName, "audio/SE_tansuowanjuwu_02");
    }

    /** 泡泡浴 */
    public playYuGang() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_03");
    }

    ////// 不倒翁
    /** 不倒翁单单次音效 */
    public playBudaowengOnce() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_10");
    }
    /** 不倒翁单连续音效 */
    public playBudaoweng() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_09");
    } 

    public stopEffect() {
        AudioManager.stopEffect();
    }

    ////// 下棋
    /** 出现音效 */
    public playXiaqiIn() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_11");
    }
    /** 画x */
    public playXiaqiX() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_13");
    }
    /** 画o */
    public playXiaqiO() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_12");
    }
    /** 失败or胜利 */
    public playXiaqiOve(isWin: boolean) {
        let audioName = isWin ? "audio/SE_tansuowanjuwu_14" : "audio/SE_tansuowanjuwu_15";
        AudioManager.playEffect(this.bundleName, audioName);
    }
    /** 泡泡机连续音效 */
    public playPaopaoJi() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_16");
    }
    /** 泡泡机戳泡泡音效 */
    public playPaoPao() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_17");

        
    }

    ////////// 玩球
    /** 掏球音效 */
    public playBallIn () {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_04");
        
    }
    /** 连续音效 */
    public playBallAudio() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_08");
    }
    /** 扔出手 */
    public playBallHand() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_06");
    }
    /** 蓄力音效 */
    public playBallEnergy() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_05");
    }

    /** 撞墙音效 */
    public playBallCollision() {
        AudioManager.playEffect(this.bundleName, "audio/SE_tansuowanjuwu_07");
    }
}
