import EventSystem from "../../../kit/system/event/EventSystem";
import ExploreGameData from "./ExploreGameData";

const {ccclass, property} = cc._decorator;

@ccclass
export default class CameraMove extends cc.Component {
    public static maxX;
    public static maxY;
    // 跟随目标
    @property(cc.Node)
    target: cc.Node = null;

    private moveSpeed: number = 5;

    private delta = 1;
    private isTouch = false;
    // 点击移动
    private clickMove = {x: false, y: false};

    
    private camera: cc.Node;

    private touchDir = cc.v2(0, 0);
    private movePos = cc.v2(0, 0);
    private 

    // private moveX: boolean;
    // private moveY: boolean;
 
    start () {
        this.camera = this.node.getChildByName("camera");
        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        EventSystem.on(ExploreGameData.MoveCamera, this.moveCamera, this)
        
        CameraMove.maxX = this.node.width / 2 - cc.winSize.width / 2;
        CameraMove.maxY = this.node.height / 2 - cc.winSize.height / 2;
    }

    update (dt) {

        if (!ExploreGameData.instance.canTouch) {
            this.clickMove = {x: false, y: false};
            this.isTouch = false; 
            return;
        }
        if (this.isTouch) {
            this.target.position = this.target.position.add(cc.v3(this.touchDir.x, this.touchDir.y).multiply(cc.v3(this.moveSpeed, this.moveSpeed)));
            this.camera.position = this.target.position;
            this.check();
            let dir = 0;
            if (this.touchDir.x > 0) {
                dir = 1
            } else if (this.touchDir.x < 0) {
                dir = -1
            }
            EventSystem.emit(ExploreGameData.PlayerMove, dir);
        }

        // 点击移动
        if (this.clickMove.x) {
            if (Math.abs(this.target.x - this.movePos.x)  <= 3) {
                this.touchDir.x = 0;
                this.clickMove.x = false;
            }
            this.check();
        }
        if (this.clickMove.y) {
            if (Math.abs(this.target.y - this.movePos.y)  <= 3) {
                this.touchDir.y = 0;
                this.clickMove.y = false; 
            } 
            this.check();
            EventSystem.emit(ExploreGameData.PlayerMove, 0);
        }
        if (!this.clickMove.x && !this.clickMove.y && this.touchDir.y == 0 && this.touchDir.x == 0) {
            this.isTouch = false; 
            EventSystem.emit(ExploreGameData.PlayerMoveEnd);
        }
    }

    public moveCamera (event: any) {
        if (event && event.data && typeof event.data.x == 'number') {
            cc.tween(this.camera)
            .to(0.3, {x: event.data.x})
            .start();
        } else {
            this.camera.x = this.target.x;    
        }
        if (event && event.data && typeof event.data.y == "number") {
            cc.tween(this.camera)
            .to(0.3, {y: event.data.y})
            .start();
        } else {
            this.camera.y = this.target.y;
        }
        this.check();
        // this.moveX = true;
        // this.moveY = true;
    }

    public onTouchStart (event: cc.Event.EventTouch) {
        if (!ExploreGameData.instance.canTouch) {
            return;
        }
        this.touchDir = cc.v2(0, 0);
        this.clickMove = {x: false, y: false};
        //this.touchPos = event.getLocation();
        
        this.setTouchDir(event.getLocation());
        this.isTouch = true;
        
        EventSystem.emit(ExploreGameData.PlayerMoveStart);
    }

    public setTouchDir (location: cc.Vec2) {
        let pos = this.node.convertToNodeSpaceAR(location);
        let dist = cc.v2(0, 0);
        dist.x = pos.x + this.camera.x;
        dist.y = pos.y + this.camera.y;

        let targetPos = cc.v2(this.target.position.x, this.target.position.y);
        let direction = dist.sub(targetPos).normalize();
        this.touchDir = direction;
    }

    public onTouchMove (event: cc.Event.EventTouch) {
        if (!ExploreGameData.instance.canTouch) {
           
            return;
        }
        this.setTouchDir(event.getLocation());
    }

    public onTouchEnd(event: cc.Event.EventTouch): void {
        if (!ExploreGameData.instance.canTouch) {
            return;
        }
        let pos = this.node.convertToNodeSpaceAR(event.getLocation());
        this.movePos.x = pos.x + this.camera.x;
        this.movePos.y = pos.y + this.camera.y;
        this.clickMove = {x: true, y: true}    
    }

    // 检查边缘
    private check (): boolean {
        if (this.camera.x > CameraMove.maxX) {
            this.camera.x = CameraMove.maxX;
            // this.moveX = false;
        }
        if (this.camera.x < -CameraMove.maxX) {
            this.camera.x = -CameraMove.maxX;
            // this.moveX = false;
        }
        if (this.camera.y > CameraMove.maxY) {
            this.camera.y = CameraMove.maxY;
            // this.moveY = false;
        }
        if (this.camera.y < -CameraMove.maxY) {
            this.camera.y = -CameraMove.maxY;
            // this.moveY = false;
        }

        if (!this.target) {
            return;
        }
        let maxXTarget = this.node.width / 2 - this.target.width / 2 - this.target.width / 2;
        let maxYTarget = this.node.height / 2 - this.target.height / 2 - this.target.height / 2;

        if (this.target.x > maxXTarget) {
            this.target.x = maxXTarget;
            this.clickMove.x = false;  
            this.touchDir.x = 0;
        }
        if (this.target.x < -maxXTarget) {
            this.target.x = -maxXTarget;
            this.clickMove.x = false;  
            this.touchDir.x = 0;
        }
        if (this.target.y > maxYTarget + this.target.height / 2) {
            this.target.y = maxYTarget + this.target.height / 2;
            this.clickMove.y = false;  
            this.touchDir.y = 0;
        }
        if (this.target.y < -maxYTarget) {
            this.target.y = -maxYTarget;
            this.touchDir.y = 0;
            this.clickMove.y = false; 
        }
    }

    onDestroy() {
        EventSystem.off(ExploreGameData.MoveCamera, this.moveCamera, this);
        this.node.off(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.off(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.off(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
    }
}
