import { ExploreGameType } from "./ExploreGameData";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ExploreGameUtil {
    public static playSpine (spinCmpt: sp.Skeleton, animName: string, loop: boolean, finishCb?: () => void, finishCount: number = 1, nextName?: string, nextLoop?: boolean) {
        if (!spinCmpt) {
            cc.warn(`playSpine:${animName}, sp.Skeleton null`);
            return;
        }
        if (animName == '') {
            cc.warn(`playSpine:${animName}`);
            return;
        }
        spinCmpt.node.active = true;
        spinCmpt.setAnimation(0, animName, loop);
        spinCmpt.setCompleteListener(null);
        
        if (finishCb) {
            let count = 0;
            spinCmpt.setCompleteListener(() => {
                if (spinCmpt.animation == animName) {
                    count ++
                    if (count >= finishCount) {
                        finishCb();
                        if (nextName && nextName != "") {
                            spinCmpt.setAnimation(0, nextName, nextLoop);
                        }
                        spinCmpt.setCompleteListener(null);
                    }
                }
            })
            return;
        }

        if (nextName && nextName != "") {
            spinCmpt.addAnimation(0, nextName, nextLoop);
        }
    }

    /** 矩形是否相交 */
    public static intersection (rectA: cc.Rect, rectB: cc.Rect) {
        return rectA.intersects(rectB);
    }

    public static gameTypeByName(type: string): ExploreGameType {
        switch (type) {
            case "game_xiaqi" :
                return ExploreGameType.Xiaqi;
            case "game_yugang" :
                return ExploreGameType.Yugang;
            case "game_budaoweng" :
                return ExploreGameType.Budaoweng;
            case "game_renqiu" :
                return ExploreGameType.Renqiu;
            case "game_paopaoji" :
                return ExploreGameType.PaoPaoji;
        }
    }
}
