import EventSystem from "../../../kit/system/event/EventSystem";
import ExploreGameData, { ExploreGameType } from "./ExploreGameData";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ExploreBaseGame extends cc.Component {

    currentGame: ExploreGameType = ExploreGameType.Default;

    camera: cc.Camera = null;

    public centerPos: cc.Vec3;
    public playerPos: cc.Vec3;
    public cameraZoomRatio: number;

    // 进入
    public onEnter (camera: cc.Camera) {
        if (!camera) {
            this.exit();
            return;
        }
        this.camera = camera;
        
        EventSystem.emit(ExploreGameData.HidePlayer);
        cc.tween(camera.node)
        .to(0.5, {x: this.centerPos.x, y: this.centerPos.y})
        .start();
    }

    // 
    public exit (data?: any) {
        cc.log("游戏结束");
        EventSystem.emit(ExploreGameData.SubGameExit, data);
    }

    /** 隐藏住场景tigo */
    public hidePlayer () {
        EventSystem.emit(ExploreGameData.HidePlayer);
    }
}
