
const {ccclass, property} = cc._decorator;

@ccclass
export default class ExploreGameData {
    // 角色移动
    public static PlayerMove = 'playerMove';
    // 角色移动开始
    public static PlayerMoveStart = 'playerMoveStart';
    // 移动停止
    public static PlayerMoveEnd = 'playerMoveEnd';
    // 隐藏角色
    public static HidePlayer = 'hidePlayer';
    // 游戏退出
    public static SubGameExit = 'subGameExit';
    // 移动镜头
    public static MoveCamera = 'moveCamera';
    // 移动TiGO到指定位置
    public static MoveTiGo = 'moveTigo';
   
    private static _instance: ExploreGameData = null;
    public static get instance(): ExploreGameData {
        if (!ExploreGameData._instance) {
            ExploreGameData._instance = new ExploreGameData();
        }
        return ExploreGameData._instance;
    }

    private _canTouch: boolean = false;
    public set canTouch (value) {
        this._canTouch = value;
    }
    public get canTouch (): boolean {
        return this._canTouch;
    }

}

/** 游戏类型 */
export enum ExploreGameType {
    Default = 'default',
    Xiaqi = 'xiaqi',
    Yugang = 'yugang',
    Budaoweng = 'budaoweng',
    Renqiu = 'renqiu',
    PaoPaoji = 'paopaoji',
}