import ResourcesManager, { ResourceType } from "../../../kit/manager/ResourcesManager";
import EventSystem from "../../../kit/system/event/EventSystem";
import ExploreBaseGame from "./ExploreBaseGame";
import ExploreGameAudio from "./ExploreGameAudio";
import ExploreGameData from "./ExploreGameData";
import ExploreGameUtil from "./ExploreGameUtil";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameBall extends ExploreBaseGame {

    public cameraZoomRatio = 1.8;

    public centerPos: cc.Vec3 = cc.v3(-1390, 674);

    private angleMove: number = 0;

    private isPlaying = false;

    private roomSpine: sp.Skeleton
    private spineTrack: sp.spine.TrackEntry;

    private isTouching: boolean;

    private progressBar: cc.ProgressBar;


    public onEnter(camera: cc.Camera) {
        this.camera = camera;

        this.progressBar = cc.find('game/progressBar', this.node).getComponent(cc.ProgressBar);
        this.progressBar.progress = 0;
        this.progressBar.node.active = false;
        cc.tween(camera.node)
        .to(0.5, {x: this.centerPos.x, y: this.centerPos.y})
        .delay(1)
        .call(() => {
            this.gaming();
            EventSystem.emit(ExploreGameData.HidePlayer);
        })
        .start();
        EventSystem.emit(ExploreGameData.MoveTiGo, {x: -1524, y: 660, dir: -1, time: 1});
    }

    public gaming () {
        let spineNode = this.node.getChildByName("game");
        this.roomSpine = spineNode.getComponent(sp.Skeleton);
        spineNode.active = true;
        this.isPlaying = false;
        let addTouch = () => {
            let touchNode = this.node.getChildByName("touchNode");
            touchNode.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
            touchNode.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
            touchNode.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);

        }
        ExploreGameAudio.instance.playBallIn();
        ExploreGameUtil.playSpine(this.roomSpine, 'T-naqiu', false, () => {
            let worldPos = this.node.convertToWorldSpaceAR(cc.v2(10, 10));
            ResourcesManager.instance.loadRes("exploreGame", "prefabs/hand", ResourceType.Normal, cc.Prefab, (e, res) => {
                this.scheduleOnce(() => {
                    if (this.isTouching) {
                        return;
                    }
                    let prefab = cc.instantiate(res);
                    this.node.addChild(prefab);
                    prefab.position = cc.v2(-1358, 700)
                    ExploreGameAudio.instance.playPress();
                }, 2)
            });
            this.scheduleOnce(() => {
                addTouch();
            }, 1)

        }, 1, 'T-naqiudaiji', true);
    }

    public exit() {
        let touchNode = this.node.getChildByName("touchNode");
        touchNode.off(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        touchNode.off(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        touchNode.off(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);

        this.roomSpine.node.active = false;
       
        super.exit({MoveCamera: true});
    }

    public onTouchStart (event: cc.Event.EventTouch) {
        if (this.isPlaying) {
            return;
        }
        ExploreGameAudio.instance.playBallEnergy();
        this.spineTrack = this.roomSpine.setAnimation(0, "T-xuli", false);
        this.angleMove = 0;
        this.spineTrack.animationStart = this.angleMove;
        this.spineTrack.animationEnd = this.angleMove;
        this.isTouching = true;
        this.progressBar.node.active = true;

        let hand = this.node.getChildByName("hand");
        if (hand) {
            hand.destroy();
        }
    }

    // public onTouchMove (event: cc.Event.EventTouch) {
    //     if (this.isPlaying) {
    //         return;
    //     }
    //     let delta = event.getDelta();
    //     this.angleMove += (-delta.x * 0.003);
    //     if (this.angleMove <= 0) {
    //         this.angleMove = 0;
    //     }
    //     if (this.angleMove >= 2.3 ) {
    //         this.angleMove = 2.3;
    //     }
    //     this.spineTrack.animationStart = this.angleMove;
    //     this.spineTrack.animationEnd = this.angleMove;
    // }
    update (dt) {
        if (!this.isTouching) {
            return;
        }
        this.angleMove += (dt * 0.6);
        if (this.angleMove <= 0) {
            this.angleMove = 0;
        }
        if (this.angleMove >= 1.3 ) {
            this.angleMove = 1.3;
        }
        let progress = this.angleMove / 1.1;
        this.progressBar.progress = progress;
        this.spineTrack.animationStart = this.angleMove;
        this.spineTrack.animationEnd = this.angleMove;
    }

    public onTouchEnd(event: cc.Event.EventTouch): void {
        if (this.isPlaying) {
            return;
        }
        this.isTouching = false;
        this.isPlaying = true;
        this.progressBar.node.active = false;
        if (this.angleMove >= 1.3) {
            cc.tween(this.node)
            .delay(1)
            .call(() => {
                ExploreGameAudio.instance.playBallHand();
            })
            .delay(0.1)
            .call(() => {
                ExploreGameAudio.instance.playBallAudio();
            })
            .start();
            ExploreGameUtil.playSpine(this.roomSpine, 'T-rengqiu2', false, () => {
                this.exit();
            })
            return;
        }
        ExploreGameAudio.instance.playBallHand();
        ExploreGameUtil.playSpine(this.roomSpine, 'T-rengqiu1', false, null, 1, 'TC-daiji', true);
        let ball = this.node.getChildByName('ball');
        let rigidBody = ball.getComponent(cc.RigidBody);
        ball.position = cc.v3(-1519, 604);
        cc.tween(this.node)
        .delay(0.2)
        .call(() => {
            ball.active = true;
            rigidBody.angularVelocity = 30;
            rigidBody.linearVelocity = cc.v2(this.angleMove * 1800, this.angleMove * 500)
        })
        .delay(7)
        .call(() => {
            ball.active = false;
            rigidBody.linearVelocity = cc.v2(0, 0);
            this.exit();
        })
        .start();
    }
}
