import ClickCmpt from "../../../kit/component/ClickCmpt";
import ResourcesManager, { ResourceType } from "../../../kit/manager/ResourcesManager";
import EventSystem from "../../../kit/system/event/EventSystem";
import ExploreBaseGame from "./ExploreBaseGame";
import ExploreGameAudio from "./ExploreGameAudio";
import ExploreGameUtil from "./ExploreGameUtil";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameXiaqi extends ExploreBaseGame {

    public cameraZoomRatio = 1;

    public centerPos: cc.Vec3 = cc.v3(1336, -26);
    public playerPos = cc.v3(1336, -26);

    private gameNode: cc.Node;

    private clickCmpt: ClickCmpt;

    private qipanData: QipanData;

    private canClick: boolean = true;

    private tigoKuhesikao: sp.Skeleton;

    private clickMap = [1, 2, 3, 4, 5, 6, 7, 8, 9];

    onEnter (camera: cc.Camera) {
        super.onEnter(camera);
        this.clickCmpt = this.node.addComponent(ClickCmpt);
        ResourcesManager.instance.loadRes('exploreGame', 'prefabs/xiaqi', ResourceType.Normal, cc.Prefab, (e, res) => {
            let prefab = cc.instantiate(res);
            this.node.addChild(prefab);
            this.gameIn();
        });
    }

    private gameIn () {
        this.gameNode =  cc.find('xiaqi/game_node', this.node);
        this.tigoKuhesikao = cc.find('xiaqi/Tigo-kuhesikao', this.node).getComponent(sp.Skeleton);

        this.qipanData = new QipanData();
        this.clickMap = [1, 2, 3, 4, 5, 6, 7, 8, 9];

        let spineNode = cc.find('xiaqi/qipan', this.node);
        let spine = spineNode.getComponent(sp.Skeleton);
        cc.tween(this.node)
        .delay(0.4)
        .call(() => {
            spineNode.active = true;
            ExploreGameAudio.instance.playXiaqiIn();
            ExploreGameUtil.playSpine(spine, 'qipan', false, () => {
                this.canClick = true;
            });
        })
        .start();
       
       
        let worldPos
        this.gameNode.children.forEach((item, index) => {
            if (index == 4) {
                worldPos = this.node.convertToWorldSpaceAR(item.position);
            }
            this.clickCmpt.onClick(item, this.onClick, this, {target: item, index: index}, false, null);
        });
        this.scheduleOnce(() => {
            cc.log("show_common_hand_guild");
            EventSystem.emit("show_common_hand_guild", worldPos);
        }, 1)
    }

    onClick (parm: {target: cc.Node, index: number}) {
        if (!this.canClick) {
            return;
        }
        EventSystem.emit("hide_common_hand_guild");
       
        this.canClick = false;
       
        cc.log("玩家下棋", parm);
        if (!this.qipanData.clickItem(parm.index, player.wanjia)) {
            this.canClick = true;
            return;
        }

        this.clickMap[parm.index] = -1;
        cc.log(this.clickMap.toString());
        ResourcesManager.instance.loadRes('exploreGame', 'prefabs/qipan_yuan', ResourceType.Normal, cc.Prefab, (e, res: cc.Prefab) => {
            let prefab = cc.instantiate(res);
            parm.target.addChild(prefab);
            ExploreGameAudio.instance.playXiaqiO();
            ExploreGameUtil.playSpine(prefab.getComponent(sp.Skeleton), 'animation', false);

            cc.tween(prefab)
            .delay(1)
            .call(() => {
                if (this.qipanData.checkGame(this.clickMap)) {
                    this.gameOver();
                    return;
                } 
                this.tigoPut();
            })
            .start(); 
        });
    }

    tigoPut () {
        let temp = [];
        this.clickMap.forEach( item => {
            if (item != -1) {
                temp.push(item);
            }
        });
        let randomIndex = Math.floor(Math.random()* temp.length);
        let putIndex = temp[randomIndex];
        let targetNode = this.gameNode.getChildByName(`qipan_item_${putIndex}`)
        this.qipanData.clickItem(putIndex - 1, player.tigo);

        this.clickMap.forEach( (item, index) => {
            if (item == putIndex) {
                this.clickMap[index] = -1;
            }
        });
        
        cc.log(this.clickMap.toString());
        ResourcesManager.instance.loadRes('exploreGame', 'prefabs/qipan_cha', ResourceType.Normal, cc.Prefab, (e, res: cc.Prefab) => {
            let prefab = cc.instantiate(res);
            targetNode.addChild(prefab);
            ExploreGameAudio.instance.playXiaqiX();
            ExploreGameUtil.playSpine(prefab.getComponent(sp.Skeleton), 'cha', false, () => {
                if (this.qipanData.checkGame(this.clickMap)) {
                    this.gameOver();
                    return;
                } 
                this.canClick = true;
            })
        });
    }

    public gameOver () {
        let winer = this.qipanData.curPlauer;
        // 平局
        if (winer == player.default) {
            cc.tween(this.node)
            .delay(1)
            .call(() => {
                this.gameNode.active = false;
                this.tigoKuhesikao.node.active = true;
                ExploreGameUtil.playSpine(this.tigoKuhesikao, 'sikao', false, () => {
                    this.tigoKuhesikao.node.active = false;
                    this.exit({MoveCamera: true});
                    this.node.destroyAllChildren();
                });
            })
            .start();
            return;
        }
        if (winer == player.wanjia) {
            let yanhua = cc.find('xiaqi/effect_yanhua', this.node);
            let cmpt = yanhua.getComponent(sp.Skeleton);
            ExploreGameAudio.instance.playXiaqiOve(true);
            ExploreGameUtil.playSpine(cmpt, 'animation', false, () => {
                this.exit({MoveCamera: true});
                this.node.destroyAllChildren();
            })
        } else {
            ExploreGameAudio.instance.playXiaqiOve(false);
            this.scheduleOnce(() => {
                this.gameNode.active = false;
                ExploreGameUtil.playSpine(this.tigoKuhesikao, 'wrong', false, () => {
                    this.exit({MoveCamera: true});
                    this.tigoKuhesikao.node.active = false;
                    this.node.destroyAllChildren();
                });
               
            }, 1)
        }
    }
}

class QipanData {
    private qipanData: QipanItem[];
    public curPlauer: player;
    private clickData: QipanItem;
   
    constructor () {
        this.qipanData = [];
        for (let i = 1; i < 10; i ++) {
            this.qipanData.push(new QipanItem(i));
        }
        
    }
    public clickItem (index, player: number): boolean {
        let item = this.qipanData[index];
        this.clickData = item;
        this.curPlauer = player;
        return item.click(player);
    }

    checkGame (clickMap: number[]): boolean {
        
        
        if (this.checkItem1(1)) {
            cc.log("这里检查1");
            return true;
        }
        if (this.checkItem2(1)) {
            cc.log("这里检查2");
            return true;
        }
        if (this.checkItem3()) {
            cc.log("这里检查3");
            return true;
        }
        let over = clickMap.find(item => item != -1);
        if (!over) {
            cc.log("结束了" + over);
            this.curPlauer = player.default;
            return true;
        }
        return false;
    }
// 1 2 3
// 4 5 6
// 7 8 9
    private checkCount = 0;

    // 检查行 
    checkItem1 (index: number): boolean {
        if (this.checkCount >= 3) {
            this.checkCount = 0;
        }
        if (index > 9) {
            return false;
        }
        
        this.checkCount ++;
        let curItem = this.qipanData[index - 1];
        let nextItem = this.qipanData[index]
        if (curItem.player == nextItem.player && nextItem.player != player.default) {
            if (this.checkCount == 2) { // 检查 1 2  2 3 相同 结束
                return true;
            }
            return this.checkItem1(index + 1);
        }
        if (curItem.player != nextItem.player || nextItem.player == player.default) {
            let nextIndex = index + 4 - this.checkCount;
            this.checkCount = 0;
            return this.checkItem1(nextIndex);
        }
    }

    // 检查竖
    checkItem2 (index: number) {
        if (this.checkCount >= 3) {
            this.checkCount = 0;
        }
        this.checkCount ++;
        let curItem = this.qipanData[index - 1];
        let nextItem = this.qipanData[index + 2]
        if (curItem.player == nextItem.player && nextItem.player != player.default) {
            if (this.checkCount == 2) { // 检查 1 2  2 3 相同 结束
                return true;
            }
            return this.checkItem2(index + 3);
        }
        if (curItem.player != nextItem.player || nextItem.player == player.default) {
            let nextIndex = index + 3 - 3 * this.checkCount + 1;// nextItemIndex * 3 * this.checkCount + 1
            this.checkCount = 0;
            if (nextIndex > 3) {
                return false;
            }
            return this.checkItem2(nextIndex);
        }
    }
// 1 2 3
// 4 5 6
// 7 8 9
    // 检查对角
    checkItem3 () {
        let item1 = this.qipanData[0];
        let item5 = this.qipanData[4];
        let item9 = this.qipanData[8];
        let item3 = this.qipanData[2];
        let item7 = this.qipanData[6];
        if ((item1.player == item5.player && item5.player == item9.player) && item5.player != player.default) {
            return true;
        }

        if ((item3.player == item5.player && item5.player == item7.player) && item5.player != player.default) {
            return true;
        }
        return false;
    }
    
}

class QipanItem {
    public id: number;
    private isClick: boolean;
    public player = player.default;

    constructor (id: number) {
        this.id = id;
    }
    
    click (player: player): boolean {
        if (this.isClick) {
            cc.log("下锅了");
            return false;
        }
        cc.log("首次点击");
        this.player = player;
        this.isClick = true;
        return true;
    }    
}

enum player {
    default,
    tigo = 1,
    wanjia = 2
}
