import ExploreGameUtil from "./ExploreGameUtil";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ExploreTigo extends cc.Component {

    private tigoSpine: sp.Skeleton;

    private isMoving: boolean;
    // 转身中
    private isTurning: boolean;

    init () {
        this.tigoSpine = this.node.getComponentInChildren(sp.Skeleton);
    }

    public lutou() {
        ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.lutou, true);
    }

    public appear(cb: () => void) {
        ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.appear, false, () => {
            if (cb) {
                cb();
            }
        }, 1, TigoSpineName.idle, true);
    }

    public move() {
        this.isMoving = true;
        if (this.isTurning) {
            return
        }
        if (this.tigoSpine.animation == TigoSpineName.prepare || this.tigoSpine.animation == TigoSpineName.move) {
            return;
        }
        ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.prepare, false, null, 1, TigoSpineName.move, true);
    }

    public setIdle () {
        this.tigoSpine.setAnimation(0, TigoSpineName.idle, true);
    }

    public idle() {
        if (this.isTurning) {
            return
        }
        this.isMoving = false;
        if (this.tigoSpine.animation == TigoSpineName.stop || this.tigoSpine.animation == TigoSpineName.idle) {
            return;
        }
        if (this.tigoSpine.animation != TigoSpineName.move) {
            let anim = this.tigoSpine.animation;
            this.tigoSpine.setMix(anim, TigoSpineName.idle, 0.2);
            ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.idle, true);
            return;
        }
        ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.stop, false, () => {
            this.tigoSpine.setMix(TigoSpineName.stop, TigoSpineName.idle, 0.2);
        }, 1, TigoSpineName.idle, true);
    }

    public turn (dir: number) {
        let curDir = this.node.scaleX;
        if (dir == curDir) {
            this.isTurning = false;
            return;
        }
        if (this.isTurning) {
            return;
        }
        this.isTurning = true;
        // 当前是idle
        if (this.tigoSpine.animation == TigoSpineName.idle || this.tigoSpine.animation == TigoSpineName.stop || this.tigoSpine.animation == TigoSpineName.prepare) {
            // 播转身动画
            ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.turn, false, () => {
                if (this.isMoving) {
                    this.isTurning = false;
                    this.node.scaleX = dir;
                    if (this.isMoving) {
                        this.move();
                    } else {
                        ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.idle, false);
                    }
                }
            });
            return;
        }

        // 飞的状态
        if (this.tigoSpine.animation == TigoSpineName.move) {
            // 播转身动画
            ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.move_turn, false, () => {
                if (this.isMoving) {
                    this.isTurning = false;
                    this.node.scaleX = dir;
                    ExploreGameUtil.playSpine(this.tigoSpine, TigoSpineName.move, true);
                }
            });
            return;
        }
        this.node.scaleX = dir;
    }

}


enum TigoSpineName {
    idle = "idle",
    lutou = "chuchang-lutou",
    appear = "chuchang-chuxian",
    move = "move",
    move_turn = 'move-turn',
    prepare = 'prepare',
    stop = 'stop',
    turn = 'turn',
 }