import ResourcesManager, { ResourceType } from "../../../kit/manager/ResourcesManager";
import EventSystem from "../../../kit/system/event/EventSystem";
import ExploreBaseGame from "./ExploreBaseGame";
import ExploreGameAudio from "./ExploreGameAudio";
import ExploreGameData from "./ExploreGameData";
import ExploreGameUtil from "./ExploreGameUtil";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameBudaoweng extends ExploreBaseGame {

    public cameraZoomRatio = 1.3;

    public centerPos: cc.Vec3 = cc.v3(-123, 724);
    //public playerPos: cc.Vec3 = cc.v3(-500, 724);

    private angleMove: number = 0;

    private budaowengBg: cc.Node;
    private budaowengSpine: sp.Skeleton;

    private isPlaying = false;
    private isBreak = false;
    private isTouch = false;

    public onEnter(camera: cc.Camera) {
        this.camera = camera;
        cc.tween(camera.node)
        .to(0.5, {x: this.centerPos.x, y: this.centerPos.y})
        .start();
        this.isTouch = false;
        let touchNode = this.node.getChildByName("touchNode");
        touchNode.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        touchNode.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        touchNode.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        touchNode.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);

        this.isPlaying = false;
        this.budaowengBg = this.node.getChildByName("bg_node");
        this.budaowengSpine = this.node.getChildByName("budaoweng").getComponent(sp.Skeleton);
        
        ResourcesManager.instance.loadRes("exploreGame", "prefabs/hand", ResourceType.Normal, cc.Prefab, (e, res) => {
            this.scheduleOnce(() => {
                if (this.isTouch) {
                    return;
                }
                let prefab = cc.instantiate(res) as cc.Node;
                this.node.addChild(prefab);
                prefab.position = this.budaowengBg.position
                prefab.y += 200;
                let spin = prefab.getComponentInChildren(sp.Skeleton);
                ExploreGameUtil.playSpine(spin, "zuoyou", true);
                ExploreGameAudio.instance.playDrag();
            }, 2)
        })

        EventSystem.emit(ExploreGameData.MoveTiGo, {x: -500, y: 724, dir: 1, time: 0.5});
    }

    public exit() {
        let touchNode = this.node.getChildByName("touchNode");
        touchNode.off(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        touchNode.off(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        touchNode.off(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        touchNode.off(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);

        this.timer = 0;
        this.isPlaying = false;
        this.budaowengBg.y = 606
        this.budaowengBg.angle = 0;
        this.angleMove = 0;
        let motorJoint = this.budaowengBg.getComponent(cc.MotorJoint);
        let rigidBody = this.budaowengBg.getComponent(cc.RigidBody);
        rigidBody.active = false;
        motorJoint.enabled = false;
        this.isBreak = false;

        cc.tween(this.camera.node)
        .to(0.3, {x: -500, y: 724})
        .call(() => {
            super.exit({MoveCamera: true});
        })
        .start();
    }

    public onTouchStart (event: cc.Event.EventTouch) {
        if (this.isBreak) {
            return;
        }
        this.break();
        this.isTouch = true;
        let hand = this.node.getChildByName("hand");
        if (hand) {
            hand.destroy();
        }
    }

    public onTouchMove (event: cc.Event.EventTouch) {
        if (this.isBreak) {
            return;
        }
        let delta = event.getDelta();
        this.angleMove = (delta.x * 0.3);
        this.budaowengBg.angle -= this.angleMove;
        if (this.budaowengBg.angle > 61) {
            this.budaowengBg.angle = 61;
        }
        if (this.budaowengBg.angle < -61) {
            this.budaowengBg.angle = -61;
        }
        this.budaowengBg.y = Math.abs(this.budaowengBg.angle * 1.1 * (this.budaowengBg.angle / 90)) + 606;
    }

    public onTouchEnd(event: cc.Event.EventTouch): void {
        if (this.isBreak) {
            return;
        }
        if (Math.abs(this.budaowengBg.angle) > 60) {
            this.isBreak = true

            this.budaowengSpine.node.active = true;
            this.budaowengBg.active = false;
            this.budaowengBg.angle = 0;
            this.budaowengBg.y = 606
            ExploreGameAudio.instance.playBudaoweng();
            ExploreGameUtil.playSpine(this.budaowengSpine, 'budaoweng', false, () => {
                this.budaowengSpine.node.active = false;
                this.budaowengBg.active = true;
                this.scheduleOnce(() => {
                    this.exit();
                }, 1)
            });
            return;
        }
        this.isPlaying = true;

        let rigidBody = this.budaowengBg.getComponent(cc.RigidBody);
        rigidBody.active = true;
        let motorJoint = this.budaowengBg.getComponent(cc.MotorJoint);
        motorJoint.enabled = true;
        ExploreGameAudio.instance.playBudaowengOnce();
    }

    private timer = 0;
    update (dt) {
        if (!this.isPlaying) {
            return;
        }
        this.timer += dt;
        if (this.timer >= 2) {
            this.exit();
        }

        if (this.budaowengBg.angle > 2) {
            this.timer = 0;
        }
    }

    // 打断
    break () {
        ExploreGameAudio.instance.stopEffect();
        this.budaowengBg.angle = 0;
        this.budaowengBg.y = 606
        let motorJoint = this.budaowengBg.getComponent(cc.MotorJoint);
        let rigidBody = this.budaowengBg.getComponent(cc.RigidBody);
        rigidBody.active = false;
        motorJoint.enabled = false;
        this.angleMove = 0;
        this.isPlaying = false;
        this.timer = 0;
    }
}
